	        animation-fill-mode: forwards;
}
@-webkit-keyframes lazy-animation{
from{
		opacity: 0;
}
to{
		opacity: 1;
}
}
@keyframes lazy-animation{
from{
		opacity: 0;
}
to{
		opacity: 1;
}
}
